<script>
  import Flexbox from 'vux/components/flexbox'
  import FlexboxItem from 'vux/components/flexbox-item'

  export default {
    data () {
      return {
        closeEnter: true,
        logined: false,
        receive: false,
        coupon: {},
        user: {},
        mobile: ''
      }
    },
    route: {
      data (transtion) {
        this.$dispatch('show-loading')

        this.$http.get('http://jiancan.me/api/u1/coupons/one.json', {'coupon_id': this.$route.params.coupon_id}).then(function (response) {
          this.$set('coupon', response.data)
        }, function (response) {
          this.$dispatch('response-msg', response)
        })

        let access_token = localStorage.getItem('jc_user_access_token')
        if (access_token != null) {
          this.$http.get('http://jiancan.me/api/u1/users/current.json', { access_token: access_token }).then(function (response) {
            this.$set('user', response.data)
            this.logined = true
          }, function (response) {
            this.$dispatch('response-msg', response)
          })
          this.$http.get('http://jiancan.me/api/u1/coupon_users/check.json', {access_token: access_token, coupon_id: this.$route.params.coupon_id}).then(function (response) {
            if (response.data.resule_code === 'SUCCESS') {
              this.receive = true
            }
          }, function (response) {
            this.$dispatch('hide-loading')
            this.$dispatch('response-msg', response)
          })
        }

        this.$dispatch('hide-loading')
      }
    },
    methods: {
      close_enter () {
        this.closeEnter = !this.closeEnter
      },
      updateMobile () {
        let access_token = localStorage.getItem('jc_user_access_token')
        this.$http.post('http://jiancan.me/api/u1/coupons.json', { access_token: access_token }).then(function (response) {
          this.$dispatch('show-tip', response)
        }, function (response) {
          this.$dispatch('response-msg', response)
        })

        if (!this.user.mobile) {
          if (access_token != null) {
            this.$http.put('http://jiancan.me/api/u1/users/current.json', { access_token: access_token, user: { mobile: this.mobile } }).then(function (response) {
            }, function (response) {
              this.$dispatch('response-msg', response)
            })
          }
        }
      },
      login () {
        window.location.href = 'http://jiancan.me/weixin/authorize?request_url=' + this.$route.name + '&coupon_id=' + this.$route.params.coupon_id
      }
    },
    components: {
      Flexbox,
      FlexboxItem
    }
  }
</script>
